.page-content {
    margin-top: 3.8rem;
}

// И вот тут погнали просто перечислять все блоки из визувига
.content-block {
    margin-bottom: 4rem;
    h2:first-child {
        margin-bottom: 3.5rem;
    }
    table {
        padding: 0;
        cellspacing: 0px;
        tr:first-child {
            th {
                text-align: left;
                font-weight: normal;
                color: $blue;
                font-size: 1.1rem;
                border-top: 1px solid $grey2;
                padding: 2rem 0;
            }
        }
        td {
            border-top: 1px solid $grey2;
            padding: 2rem 0;
        }
    }
}

.content-block_example {
    margin: 1.5rem 0;
    padding: 2rem;
    border-radius: .5rem;
    background-color: $white;
    color: $blue;
}

.content-block_note {
    padding: 1.5rem 2rem;
    border-radius: .5rem;
    color: $white;
    background: linear-gradient(90deg, #1134B5 0%, #2353E0 75%);
    font-size: 1.5rem;
    strong {
        color: $blue2;
        font-weight: normal;
    }
}

.content-block_list-table {
    margin: 2rem 0;
}

.content-block_list-table_item {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    padding: 2rem 0;
    border-top: 1px solid $grey2;
    .content-block_list-table_left {
        h4 {
            font-size: 1.45rem;
            color: $blue;
        }
        p {
            font-size: 1.25rem;
        }
    }
    .content-block_list-table_right {
        h5 {
            color: $blue;
        }
    }
}