.block-list {
    margin: 1rem 0;
}

.block-list_item {
    padding-left: 5rem;
    position: relative;
    display: flex;
    align-items: center;
    &:not(:last-child) {
        margin-bottom: 4rem;
    }
}

.block-list_item_counter {
    position: absolute;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $blue2;
    width: 2.5rem;
    height: 2.5rem;
    border: 1px solid $blue2;
    border-right: 1px solid;
    border-left-color: transparent;
    border-bottom-color: transparent;
    border-radius: 100%;
    transform: rotate(45deg) translateX(-.5rem) translateY(.5rem);
    font-size: 1rem;
    font-weight: 100;
    span {
        transform: rotate(-45deg) translateX(-.1rem);
    }
}