.lesson-item {
    display: flex;
    flex-wrap: wrap;
    padding: 1.5rem 2.3rem;
    border-radius: .5rem;
    background-color: $white;
    &:not(:last-child) {
        margin-bottom: 1.5rem;
    }
}

.lesson-item_desc {
    h3 {
        color: $blue;
    }
}

.lesson-item_meta {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    .lesson-delay {
        padding: .2rem 0rem .2rem 1.1rem;
    }
}

.lesson-counter {
    border-radius: .5rem;
    background-color: rgba($blue, .05);
    color: $blue;
    font-size: .65rem;
    padding: .2rem 1.5rem;
}

.lesson-delay {
    color: $blue;
    font-size: .65rem;
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 1.2rem;
    &:before {
        content: '';
        position: absolute;
        left: 0;
        width: 1rem;
        height: 1rem;
        background: url(../img/icons/clock.svg) center center no-repeat;
        top: auto;
        bottom: auto;
    }
}

@media only screen and(max-width: 768px) {
    .lesson-item {
        padding: 2rem;
        .button {
            margin-top: 2rem;
        }
    }

    .lesson-item_meta {
        display: block;
        margin-top: 1.5rem;
    }
}