.block-difficulty {
    display: flex;
    align-items: center;
}

.block-difficulty_title {
    margin-right: 1rem;
    color: $blue;
    font-size: 1.15rem;
}

.block-dots {
    display: flex;
}

.block-dots_item {
    width: .5rem;
    height: .5rem;
    border-radius: 100%;
    background-color: rgba($blue2, .3);
    &:not(:last-child) {
        margin-right: .5rem;
    }
    &--active {
        background-color: rgba($blue2, 1);
    }
}