.block-review {
    display: grid;
    grid-template-columns: repeat(2,2fr);
    grid-gap: 1rem;
    .block-review_item {
        &:not(:last-child) {
            margin-bottom: 1.5rem;
        }
    }
    .page-lesson_review-title {
        grid-column: 1/3;
        margin-bottom: 1rem;
    }
}

.stars-counter_point {
    margin-right: .5rem;
}

.block-review_item-title {
    margin-bottom: 1.5rem;
}

.stars-counter {
    display: flex;
    align-items: center;
    .start-counter_item {
        cursor: pointer;
        path {
            transition: all .1s ease-in-out;
        }
        &:not(:last-child) {
            margin-right: 1rem;
        }
        &--active,
        &:hover {
            path {
                fill: $green;
            }
        }
    }
}