.lesson-start {
    height: 73vh;
    position: relative;
}

.lesson-start_desc {
    position: relative;
    color: $white;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    max-height: 100%;

    padding-bottom: 3rem;
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: -5vw;
        width: calc(100% + 5vw);
        height: 50%;
        background: linear-gradient(180deg, rgba(4, 40, 138, 0.0001) 0%, #04288A 100%);
    }
    .section-title,
    .block-difficulty {
        position: relative;
        z-index: 1;
        
    }
    .block-difficulty {
        margin-top: 1.5rem;
    }
    .block-difficulty_title {
        color: $white;
    }
}

.lesson-start_bg {
    position: absolute;
    top: 0;
    left: -5vw;
    width: calc(100% + 5vw);
    height: 100%;
    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

// Список уроков с прокруткой справа
.lesson-start_list {
    max-height: 100%;
    //overflow: hidden;
}
.lesson-start_list_title {
    padding: 1.75rem 1rem;
    color: $blue;
    font-size: 1.5rem;
}

.lesson-list-scroll {
    height: calc(100% - 5.7rem);
    width: calc(100% + 5vw);
    overflow-y: scroll;
    overflow-x: hidden;
    &::-webkit-scrollbar {
        width: 6px;
    }
    &::-webkit-scrollbar-thumb {
        border-width: 1px 1px 1px 2px;
        border-color: #fafafa;
        background-color: #444;
    }
    
    &::-webkit-scrollbar-thumb:hover {
        border-width: 1px 1px 1px 2px;
        border-color: #fafafa;
        background-color: #373A3C;
    }
    
    &::-webkit-scrollbar-track {
        border-width: 0;
    }
}

.lesson-list-scroll_item {
    padding: 1.75rem 1rem;
    position: relative;
    display: block;
    text-decoration: none;
    &:after {
        content: '';
        position: absolute;
        top: 0;
        right: -5vw;
        width: calc(100% + 5vw);
        height: 100%;
        background: linear-gradient(90deg, #1134B5 0%, #2353E0 75%);
        opacity: 0;
        transition: all .25s ease-in-out;
        z-index: -1;
    }
    &--active,
    &:hover {
        color: $white;
        &:after {
            opacity: 1;
        }
        .lesson-delay {
            color: $white;
        }
    }
}

.lesson-list-scroll_item_title {
    margin-bottom: .75rem;
}

// Задания к уроку
.page-lesson_task {
    h2 {
        margin-bottom: 3.5rem;
    }
}