.sidebar-widget {
    padding: 2rem;
    border-radius: .5rem;
    background-color: #fff;
    &:not(:last-child) {
        margin-bottom: 2rem;
    }
    h3 {
        font-size: .85rem;
        margin-bottom: 1rem;
    }

    // перезаписываем стили для блока отзывов
    .block-review {
        grid-template-columns: repeat(1, 1fr);
    }
    .block-review_item-title {
        margin-bottom: .5rem;
        font-size: .85rem;
    }
    .start-counter_item {
        width: 1.35rem;
        height: 1.35rem;
        &:not(:last-child) {
            margin-right: .5rem;
        }
        svg {
            width: 100%;
            height: 100%;
        }
    }
}