// стили, характерные только для этой страницы
.page-course {
    .start-course {
        margin-top: -2rem;
        position: relative;
        z-index: 1;
    }
}

// первый блок
.page-course_hello {
    padding: 6rem 0;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    .section-title {
        color: $blue;
        margin-bottom: 2rem;
    }
}

.hello-block_desc {
    font-size: 1.6rem;
    margin-bottom: 2rem;
}

// Блок с вопросами

.block-questions {
    position: relative;
    .wrapper {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        &:before,
        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: -5vw;
            width: calc(50% + 5vw);
            height: 100%;
            background: linear-gradient(90deg, #1134B5 0%, #2353E0 75%);
        }
        &:after {
            left: auto;
            right: -5vw;
            transform: rotate(180deg);
            background: #28b0fe;
        }
    }
}

.block-questions_left {
    position: relative;
    padding: 6rem 0;
    padding-left: 1rem;
    .block-list_item {
        color: $white;
        font-size: 1.3rem;
    }
}

.block-questions_right {
    padding: 4rem 0;
    position: relative;
    overflow: hidden;
    img {
        position: absolute;
        top: 0;
        left: 5vw;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
    }
}

// Блок с описанием "пройти курс"
.start-course {
    .wrapper {
        border-radius: .5rem;
        text-align: center;
        padding: 3rem;
        background-color: $white;
    }
    .start-course_desc {
        margin-bottom: 3rem;
        font-size: 1.2rem;
    }
}

// Блок с иконками
.course-desc {
    .wrapper {
        display: grid;
        grid-template-columns: repeat(3,3fr);
        grid-gap: 0rem;
    }
    .course-desc_item {
        width: 75%;
        margin: auto;
    }
}

.emoji-item {
    text-align: center;
    .emoji-item_title {
        margin: 1rem 0;
        color: $blue;
        font-size: 1.5rem;
    }
    .emoji-item_desc {
        font-size: .85rem;
    }
}

// Блок со списком уроков
.lesson-list {
    margin: 4rem 0;
    .section-title {
        text-align: center;
        color: $blue;
    }
}


// для обмилок
@media only screen and(max-width: 768px) {
    .block-questions {
        .wrapper:before {
            width: 100%;
            left: 0;
        }
        .wrapper:after {
            content: none;
        }
    } 

    .course-desc {
        .wrapper {
            grid-template-columns: repeat(1, 1fr);
            grid-gap: 5rem;
        }
    }

    .start-course {
        .wrapper {
            padding: 1rem;
        }
    }
}